	module.exports = {"data":{"markdownRemark":{"html":"<p>本名は“野村 周平”、ペンネームは“Mc(まく)”です。このペンネームは2005年辺りから今に至るまで使っています。 (最近は割とペンネームよりも本名晒す方が多かったりしますが) ペンネームの由来は建前上私の芸名“眞久 秀(まひさ すぐる)”の略称、と言うことにしていますが(ちなみに芸名もその場の思い付きでのでっち上げです)、実は2000年～2004年辺りに使っていた“MAKINA(まきな)”の略称です。2005年辺りからみょんなことで同人関係の音屋さんと仲良くする機会がありまして、「そういえば俺テクノやトランスばっかりで<a href=\"http://ja.wikipedia.org/wiki/%E3%83%9E%E3%82%AD%E3%83%8A_(%E9%9F%B3%E6%A5%BD)\">ハピコア</a>なんて作ったことないぞ」ってことに気付いて、慌ててでっち上げたペンネームがこれだったりします。  軽くペンネームの変遷を紹介します。どうでもいい方は読み飛ばしてください。 1996年頃までは本名、或いはイニシャルで活動していましたが、1997年末辺りから“野菜村”というペンネームを使うようになりました。小学校の時給食にたまーに出ていた乾燥野菜のスナック菓子から取った名前です。翌年からはその略称として“YASM(YASaiMura)”に変更(<a href=\"http://yasm.tortall.net/\">某アセンブラ</a>とは一切関係ありません)、さらに翌年の1999年辺りには“やさい”とか“やさいむら”とか平仮名表記になったり、“相浦(yasAImURA)”→“愛裏”→“らぶ★ればーす”→“Luv-Reverse”となったりとかコロコロ変えていた時期がありました。 で、最終的に1999年末にペンネームをコロコロ変えるの止めようと思い始めて、当時の自作ゲームの中では代表作だった“<a href=\"/mk-iik\">薪割り2K(後にMk-IIKに改称)</a>”や“<a href=\"/choppin\">牛チョップ</a>”を連想しつつ、最初のペンネーム“野菜村”に原点回帰するという意味で“牧菜(後にMAKINA)”となりました。</p>","frontmatter":{"cover":null,"date":"2012-11-27T22:50:29+09:00","strDate":"2012/11/27","title":"代表の名前、およびペンネーム"}}},"pathContext":{"path":"/ja/blog/2012/11/27-代表の名前、およびペンネーム/","langKey":"ja"}}

